.categories-container{position:relative;font-size:0;padding:0 .2rem;box-sizing:border-box;overflow-x:hidden;height:292px}.categories-container .category-container{width:33.33%;display:inline-block;padding:.2rem;box-sizing:border-box}.categories-container .category{position:relative;display:inline-block;width:100%;padding-bottom:75%;background-size:cover;background-repeat:no-repeat;background-position:center;cursor:pointer;border-radius:.6rem;overflow:hidden}.categories-container .category-name{display:flex;align-items:center;justify-content:center;width:100%;height:100%;position:absolute;background-color:rgba(0,0,0,0.2);color:white;font-size:1.2rem;font-weight:bold;text-align:center;text-transform:uppercase}
